  /* --startColor: #ccd9ce;
  --endColor: #7fada9;
  --accentColor: #19afbf; */

  --appstartColor: #171f26;
  --appendColor: #81878c;
  --accentColor: #f0f1f2;

  --startColor: transparent;
  --endColor: transparent;
  --accentColor: #f0f1f2;

  --projectbackground: rgba(255, 255, 255, 0.9);
  --projectbackgroundHover: rgb(255, 255, 255);
