::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background-image: linear-gradient(#171f26, #81878c);
  background-image: linear-gradient(var(--appstartColor), var(--appendColor));
}

/* Handle */
::-webkit-scrollbar-thumb {
  /* background: rgba(134, 173, 144, 0.4); */
  /* background: rgba(225, 225, 0, 0.4); */
  background-color: #f2622e;
  background-color: var(--buttonPriColor);
  opacity: 0.4;
}

